<template>
  <div>
  <p>
    <input name="word" v-model="w" v-on:keyup.enter="getData()" >
    <button @click="getData()">search</button><br>
  </p>
    
 
  <table>
    <thead>
     
    </thead>
    <tbody>
      <tr v-for="(word) in data" v-bind:key="word" >
        <td>
          <p name="ss">{{word.word}}</p>
        </td>
        <td>
           <p name="tt">{{word.translation}}</p>
        </td>
      </tr>
    </tbody>
  </table>    

  </div>
</template>

 
 <script>
export default {
  name: 'dictionary',
   data() {
        return{
          data: ""
          }        
        },
  methods: {
    getData() {

      var that=this;
        this.data="111";
        console.log("click");
        // http://localhost:18080/api/getData
        this.axios.get('/api/getData', {
            params: {
              ID: this.w
            },
          })
          .then(function (response) {
          
            that.data=response.data;
            console.log(response);
          })
          .catch(function (error) {
            console.log(error);
          });
          }
  },
}
</script>

